import { Injectable } from '@angular/core';
import { AppStateService } from '@c8y/ngx-components';
import { FetchClient, IManagedObject, IResultList, Service } from '@c8y/client';

@Injectable()
export class AdvancedSoftwareService extends Service<IManagedObject> {
  private static readonly applicationName = 'advanced-software-mgmt';

  protected baseUrl = 'service/advanced-software-mgmt';
  protected listUrl = 'software';
  protected propertyName = 'softwareList';

  private isAvailable: Promise<boolean>;

  constructor(client: FetchClient, private appState: AppStateService) {
    super(client);
  }

  list(filter: object = {}): Promise<IResultList<IManagedObject>> {
    return super.list(filter);
  }

  isASMAvailable(): Promise<boolean> {
    if (this.isAvailable === undefined) {
      this.isAvailable = this.appState.isApplicationAvailable(
        AdvancedSoftwareService.applicationName
      );
    }

    return this.isAvailable;
  }
}
