import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { IManagedObject, IResultList, QueriesUtil } from '@c8y/client';
import { ForOfFilterPipe, gettext, TypeaheadComponent } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { uniqBy } from 'lodash-es';
import { BehaviorSubject, Observable, pipe } from 'rxjs';
import { debounceTime, map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { RepositoryType } from '../repository.model';
import { RepositoryService } from '../repository.service';

@Component({
  selector: 'c8y-software-type',
  templateUrl: 'software-type.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => SoftwareTypeComponent)
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => SoftwareTypeComponent),
      multi: true
    }
  ]
})
export class SoftwareTypeComponent implements OnInit, ControlValueAccessor, Validator {
  @Input() softwareTypeMO: IManagedObject;

  @Input() disabled: boolean;

  @Input() style;

  @Input()
  required = true;

  @Input()
  placeholder: string = this.translateService.instant(gettext('e.g. {{ example }}'), {
    example: 'yum'
  });

  @Input()
  emitResultsOnly = false;

  @Input()
  showBtnInNotFoundMessage = true;

  @Input()
  allowFreeEntries = true;

  @Input()
  showClearSelectionOption = false;

  @Input()
  clearSelectionOptionLabel: string = gettext('All software types');

  @ViewChild('deviceSoftwareTypeModel') deviceSoftwareTypeModel: TypeaheadComponent;

  @ViewChild('notFoundTypeAddNewTemplate', { static: true })
  notFoundTypeAddNewTemplate: TemplateRef<unknown>;

  @ViewChild('notFoundTypeTemplate', { static: true })
  notFoundTypeTemplate: TemplateRef<unknown>;

  @Output() onSelectSoftware: EventEmitter<IManagedObject> = new EventEmitter<IManagedObject>();

  notFoundTemplateToUse: TemplateRef<unknown>;

  filterPipe: ForOfFilterPipe<IManagedObject> = pipe(tap());
  softwaresResult$: Observable<IResultList<IManagedObject>>;
  search$: BehaviorSubject<string> = new BehaviorSubject(null);
  softwaresResult: IResultList<IManagedObject>;

  private queriesUtil: QueriesUtil = new QueriesUtil();
  private softwareTypes: Set<string> = new Set();

  constructor(
    private repositoryService: RepositoryService,
    public changeDetectorRef: ChangeDetectorRef,
    private translateService: TranslateService
  ) {
    this.softwaresResult$ = this.search$.pipe(
      debounceTime(300),
      tap(() => this.softwareTypes.clear()),
      switchMap((searchString: string) => {
        if (!this.emitResultsOnly || !searchString) {
          this.onSelectSoftware.emit(this.softwareTypeMO);
        }
        return this.getSoftwareByTypeResult(searchString);
      }),
      shareReplay(1)
    );

    this.filterPipe = pipe(map(this.removeDuplicatesBySoftwareType.bind(this)));
  }

  ngOnInit(): void {
    this.notFoundTemplateToUse = this.showBtnInNotFoundMessage
      ? this.notFoundTypeAddNewTemplate
      : this.notFoundTypeTemplate;
  }

  getSoftwareByTypeResult(searchString: string) {
    let query = this.queriesUtil.prependOrderbys({}, [{ softwareType: 1 }]);
    const filter = !!searchString
      ? {
          softwareType: {
            __eq: `*${searchString}*`
          }
        }
      : {
          __has: 'softwareType'
        };
    query = this.queriesUtil.addAndFilter(query, filter);

    return this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, {
      query,
      params: {
        pageSize: 200
      }
    });
  }

  selectSoftware(software) {
    this.softwareTypeMO = software;
    this.onSelectSoftware.emit(software);
    this.deviceSoftwareTypeModel.searchControlModel.control.markAsDirty();
    (this.deviceSoftwareTypeModel as any).onChange(software);
  }

  clearSoftware() {
    this.softwareTypeMO = undefined;
    this.search$.next('');
    this.onSelectSoftware.emit();
  }

  resetInput() {
    this.deviceSoftwareTypeModel.reset();
  }

  writeValue(value: any): void {
    this.deviceSoftwareTypeModel.writeValue(value);
  }

  registerOnChange(fn: any): void {
    this.deviceSoftwareTypeModel.registerOnChange(fn);
  }

  registerOnTouched(fn: any): void {
    this.deviceSoftwareTypeModel.registerOnTouched(fn);
  }

  setDisabledState(isDisabled: boolean): void {
    this.deviceSoftwareTypeModel.setDisabledState(isDisabled);
  }

  validate(control: AbstractControl<any, any>): ValidationErrors {
    return this.deviceSoftwareTypeModel.validate(control);
  }

  private removeDuplicatesBySoftwareType(list: IManagedObject[]): IManagedObject[] {
    const uniqueBySoftwareType: IManagedObject[] = uniqBy(list, 'softwareType').filter(
      (sw: IManagedObject) => !this.softwareTypes.has(sw.softwareType)
    );
    uniqueBySoftwareType.forEach((sw: IManagedObject) => this.softwareTypes.add(sw.softwareType));
    return uniqueBySoftwareType;
  }
}
